* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	scroll-behavior: smooth;
}

main {
	display: flex;
	align-items: center;
	flex-direction: column;
  justify-content: first;
	padding-top: 50px;

	audio {
		transition:all 0.5s linear;
		opacity: 20%;
	}

	audio:hover {
		opacity: 80%;
	}
}


canvas {
	z-index: -1;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
  height: 100%;
  border: 0;
  overflow: hidden;
  display: block;
}

